export default {
  name: 'AdminAuthPage',  
  layout: 'admin',  
  data() {
    return {
      isLogin: true,
      email:'',
      password:''
    }
  },
  methods: {
    onSubmit() {
      this.$store.dispatch('authenticateUser', {
        isLogin: this.isLogin,
        email: this.email,
        password: this.password
      })
      .then(() => {        
        this.$router.push('/admin')
      })
      .catch(e => console.log(e))
    } 
  }
}

